@import 'mixins';
@import 'fonts';
@import 'colors';

html {
	font-size: 62.5%; }

.section {
	position: relative;
	display: flex;
	align-items: center;
	flex-direction: column;
	padding: 10rem 2rem;
	&__title {
		color: $black;
		font-family: "Droid Serif";
		font-size: 3.5rem;
		font-weight: 400;
		text-transform: capitalize;
		margin-bottom: 1.8rem;
		text-align: center; }
	&__descr {
		color: $gray-1;
		font-family: Muli;
		font-size: 1.2rem;
		font-weight: 300;
		text-transform: uppercase;
		margin-bottom: 10rem;
		text-align: center;
		max-width: 51.1rem;
		@media (max-width: 36em) {
			font-size: 1.6rem;
			margin-bottom: 5rem; }
		&--normal {
			font-size: 1.5rem;
			font-weight: 300;
			line-height: 2.5rem;
			text-transform: none; } }
	&__btn {
		display: block;
		width: max-content;
		border-radius: 3px;
		border: 1px solid $purple;
		text-align: center;
		padding: 1.4rem 4.7rem;
		color: $gray-1;
		font-family: Lora;
		font-size: 1.4rem;
		font-weight: 400;
		text-transform: uppercase;
		white-space: nowrap;
		margin-top: 2.1rem;
		transition: 0.4s;
		@media (max-width: 36em) {
			font-size: 2rem; }
		&:hover {
			background: $purple;
			color: white; } } }

.btn {
	display: block;
	color: #ffffff;
	border-radius: 0.3rem;
	background-color: $purple;
	width: max-content;
	text-transform: capitalize;
	white-space: nowrap;
	font-weight: 400;
	&:hover {
		color: white; } }

.layer {
	@include layer; }

.wrap-line {
	display: flex;
	justify-content: space-between;
	align-items: center;
	flex-wrap: wrap;
	@media (max-width: 56.25em) {
		flex-direction: column; } }

@import 'blocks/nav';
@import 'blocks/product';
@import 'blocks/banner';
@import 'blocks/post';
@import 'sections/header';
@import 'sections/mailing';
@import 'sections/footer';
